.marquee {
    padding-top: 5%;
      overflow: hidden;
    position: relative;
  }
  
  .marquee div {
    display: block;
    width: 200%;
    height: 100%;
    position: absolute;
    -webkit-animation: marquee 15s linear infinite;
            animation: marquee 15s linear infinite;
  }
  
  .marquee span {
    float: left;
    width: 50%;
    padding-top: 1rem;
  }
  
  @-webkit-keyframes marquee {
    0% { left: 0; }
    100% { left: -100%; }
  }
  
  @keyframes marquee {
    0% { left: 0; }
    100% { left: -100%; }
  }
.board-text {
    text-align: center;
}

.blink_me {
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite;
  }
  
  @-webkit-keyframes blinker {
    50% {
      opacity: 0;
    }
  }
  
  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }
.fullHeight {
    height: 100%;
}
