.marquee {
    padding-top: 5%;
      overflow: hidden;
    position: relative;
  }
  
  .marquee div {
    display: block;
    width: 200%;
    height: 100%;
    position: absolute;
    animation: marquee 15s linear infinite;
  }
  
  .marquee span {
    float: left;
    width: 50%;
    padding-top: 1rem;
  }
  
  @keyframes marquee {
    0% { left: 0; }
    100% { left: -100%; }
  }